import React, {useContext, useState} from 'react'
import {Button, Form} from 'react-bootstrap';
import Select from 'react-select';
import {SalesContext} from '../../contexts/SalesContext';
import {GroupsContext} from "../../contexts/GroupsContext";
import {VendorsContext} from "../../contexts/VendorsContext";
import {LocalsContext} from "../../contexts/LocalsContext";

const AddSaleForm = () => {
    const {addSale} = useContext(SalesContext);
    const {groups} = useContext(GroupsContext)
    const {vendors} = useContext(VendorsContext)
    const {locals} = useContext(LocalsContext)

    const [vendor, setVendor] = useState('')
    const [group, setGroup] = useState('')
    const [local, setLocal] = useState('')

    const groupsOpt = [...groups.map((opt) => ({ value: opt.id, label: opt.name }))]
    const vendorsOpt = [...vendors.map((opt) => ({ value: opt.id, label: opt.name }))]
    const localsOpt = [...locals.map((opt) => ({ value: opt.id, label: opt.name }))]

    const handleSubmit = (e) => {
        e.preventDefault();
        addSale(vendor, group, local);
    }

    return (
        <Form onSubmit={handleSubmit}>
            <Form.Group>
                <Select placeholder="هەڵبژاردن..." name="group" className="form-control" options={groupsOpt} onChange={(e) => setGroup(e.value)}/>
            </Form.Group>
            <Form.Group>
                <Select placeholder="هەڵبژاردن..."  name="vendor" className="form-control" options={vendorsOpt} onChange={(e) => setVendor(e.value)}/>
            </Form.Group>
            <Form.Group>
                <Select placeholder="هەڵبژاردن..."  name="local" className="form-control" options={localsOpt} onChange={(e) => setLocal(e.value)}/>
            </Form.Group>
            <Button variant="success" type="submit">
                زیادکردنی داواکاری
            </Button>
        </Form>
    )
}

export default AddSaleForm
